<template>
  <div>
    <el-row>
      <el-col>
        <el-table :data="answers" style="width: 100%">
          <el-table-column label="问卷ID" prop="paperID" v-if="show = false"></el-table-column>
          <el-table-column label="问卷名称" prop="paperName"></el-table-column>
          <el-table-column label="最终得分" prop="finalPoint"></el-table-column>
          <el-table-column label="提交时间" prop="createTime"></el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <el-row>
      <el-col>
        <el-pagination
          background
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="5"
          layout="total, prev, pager, next"
          :total="total"
        ></el-pagination>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      currentPage: 1,
      total: 0,
      answers: []
    };
  },
  methods: {
    handleCurrentChange(current) {
      this.currentPage = current;
      var api = "http://localhost:8888/paper/answer/" + current + "/" + 5;
      this.axios
        .get(api)
        .then(response => {
          console.log("分页响应对象：" + response);
          this.answers = response.data.records;
          this.total = response.data.total;
        })
        .catch(error => {
          console.log(error);
        });
    },
    getData() {
      var api =
        "http://localhost:8888/paper/answer/" +
        sessionStorage.getItem("userID") +
        "/" +
        this.$route.params.current +
        "/" +
        this.$route.params.size;
      this.axios
        .get(api)
        .then(response => {
          console.log(response);
          this.answers = response.data.records;
          this.total = response.data.total;
        })
        .catch(error => {
          console.log(error);
        });
    }
  },
  created() {
    this.getData();
  }
};
</script>

